

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./img_source/BackGround_2.png);
  background-size: cover;
}

footer {
  position: fixed;
  left: 0.3%;
  top: 98%;
  color: rgba(255,255,255,0.4);
  font-size: 13px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  position: relative;
  top: 1.2px;
  border: 0.5px solid rgba(2, 17, 37, 0.58);
	background-color:rgba(41, 218, 218, 0.555);
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:14px;
	font-weight:bold;
	padding:3px 30px;
	text-decoration:none;
  text-shadow:3px 3px 0px #3d768a;
  border-radius:5px;
}
.btn:hover {
  border: 0.5px solid rgba(31, 94, 177, 0);
  background-color:#408c993d;
}
.btn:active {
	position:relative;
	top:1px;
}

.btn-done {
  background-color:rgba(146, 63, 194, 0.932);
}

.wrap {
    display: grid;
    grid-template-columns: 12% 62% 26%;
    height: 780px;
}

.wrap > * {
  margin: 2px;
}

.titleimg {
  width: 300px;
  margin-top: 20%;
  margin-left: 10%;
}

.moonimg {
  height: 100vh;
  display: block;
  position: absolute;
  top: -750px;
  animation: spin 160s infinite linear;
  pointer-events: none;
  margin-left: 450px;
}

@keyframes spin {
  0%  {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.taskText {
  width: 300px;
}

.duringText {
  width: 100px;
}

.textbox {
  text-align: center;
}

